import React from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import Menu from './menu'
import About from './page/about';
import EmotSvg from './page/example-1-emot';
import config from './containers/config';
import './App.css';

class App extends React.Component {
    
    render() {
        console.log(this.props);
        return (
            <div id="d3-learn">
                <BrowserRouter>
                    <React.Fragment>
                        <Menu/>
                        <div id="page-content">
                            <Switch>
                                <Route exact path={`${config.routerPrefix}/`} component={About}/>
                                <Route exact path={`${config.routerPrefix}/emot`} component={EmotSvg}/>
                            </Switch>
                        </div>
                    </React.Fragment>
                </BrowserRouter>
            </div>
        );
    }
}

export default App;